<template>
    <div>
        <TopNav mdl="sms" select="1"></TopNav>
        <div class="wyu-tips wyu-tips-warning">
            <p>1).这里是短信发送记录</p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                当前短信剩余：<span style="color: red;font-size: xx-large">{{sms_num}}</span> 条
            </div>
            <div class="wyu-block" v-loading="loading">
                <el-form :inline="true" :model="search" class="search-form" >
                    <el-form-item label="短信类型">
                        <el-select size="small" v-model="search.cat" placeholder="选择短信类型">
                            <el-option label="全部类型" :value="-999"></el-option>
                            <el-option v-for="(item,key) in smsType" :label="item" :value="key"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="开始时间">
                        <el-date-picker v-model="search.bgTime" type="datetime" placeholder="选择日期时间"></el-date-picker>
                    </el-form-item>

                    <el-form-item label="结束时间">
                        <el-date-picker v-model="search.endTime" type="datetime" placeholder="选择日期时间"></el-date-picker>
                    </el-form-item>

                    <el-form-item>
                        <el-button size="small" @click="seachDatas" type="primary" >搜索</el-button>
                    </el-form-item>
                </el-form>

                <el-table :data="logList" style="width: 100%">
                    <el-table-column width="100" prop="log_id" label="ID"></el-table-column>

                    <el-table-column label="手机号码">
                        <template slot-scope = "scope" >
                            <el-input type="textarea" resize="none" :value="scope.row.tels"></el-input>
                        </template>
                    </el-table-column>

                    <el-table-column label="发送的内容">
                        <template slot-scope = "scope" >
                            <el-input type="textarea" resize="none" :value="scope.row.text"></el-input>
                        </template>
                    </el-table-column>

                    <el-table-column label="每人次消耗短信数" prop="each_num"></el-table-column>

                    <el-table-column label="短信字数" prop="size"></el-table-column>

                    <el-table-column label="合计条数" prop="num"></el-table-column>

                    <el-table-column label="短信类型" prop="cat"></el-table-column>

                    <el-table-column label="发送时间" prop="add_time"></el-table-column>
                </el-table>
                <div style="text-align: right" class="block">
                    <el-pagination @current-change="setPage" :page-size="limit"
                                   layout="total, prev, pager, next" :total="count">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import * as api from '@/api.js';
    import TopNav from "@/components/TopNav";
    export default {
        name: "smsLog",
        components: {TopNav},
        data: function() {
            return {
                search:{
                    bgTime:'',
                    endTime:'',
                    cat:-999,
                },
                page:1,
                limit:10,
                count:0,
                logList:[],
                smsType: 0,
                sms_num: 0,
                loading:false,
                datas: [

                ],
            };
        },
        created() {
            this.getLogList();
        },
        methods: {
            setPage(res){
                this.page = res;
                this.getLogList();
            },
            seachDatas(){
                this.page = 0;
                this.getLogList();
            },
            getLogList(){
                var that = this;
                that.loading = true;
                api.post("/shop/admin.marketing.sms/getsmslog",{
                    appToken: that.$store.getters.appToken,
                    cat:that.search.cat,
                    bgTime:that.search.bgTime,
                    endTime:that.search.endTime,
                    page:this.page,
                }).then(res=>{
                    that.logList = res.list;
                    that.count=res.count;
                    that.limit= res.limit;
                    that.smsType= res.smsType;
                    that.sms_num= res.sms_num;
                    that.loading = false;
                }).catch((error)=>{
                    that.loading = false;
                });
            },

        }
    };
</script>
